import axios from 'axios'
import store from '../store/index'
import router from '../router/index'


//axios
axios.defaults.timeout = 5000; //5000
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

//axios
const instance = axios.create();
instance.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

axios.interceptors.request.use = instance.interceptors.request.use;

//request
instance.interceptors.request.use(
  config => {
    if(store.state.token){
      config.headers.Authorization = `token ${store.state.token}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);
//respone
instance.interceptors.response.use(
  response => {
    return response;
  },
  error => { //2XX
    if(error.response){
      switch(error.response.status){
        case 401:
          store.dispatch('UserLogout'); //token
          router.replace({ //
            path: 'login',
            query: { redirect: router.currentRoute.fullPath } // path
          });
      }
    }
    return Promise.reject(error.response.data);
  }
);

export default {
  //
  userRegister(data){
    return instance.post('/api/register', data);
  },
  //
  userLogin(data){
    return instance.post('/api/posts', data);
  },
  //
  getUser(){
    return instance.get('/api/user');
  },
  //
  delUser(data){
    return instance.post('/api/posts', data);
  },

  gameclasify(data){
    return instance.get('/api/gameclasify',data);
  }/*,
  selects(data){
    return instance.get('/api/routk',data);
  }*/
}



